<template>
    <div class="navigation">
        <nav class="navbar navbar-fixed-top">
            <div class="container-fluid">
                <div class="navbar-header nav navbar-nav">
                    <button @click.native="showHide()" type="button" class="navbar-toggle" data-toggle="collapse"
                            data-target="#myNavbar">
                        <i style="color: #757575; " data-feather="menu"></i>
                    </button>
                    <router-link class="navbar-brand" to="/"><img src="../../assets/images/logo-8go.svg" alt=""
                                                                  class="img-brand img-responsive">
                    </router-link>
                </div>
                <div class="collapse navbar-collapse " v-bind:class="this.isHide" id="myNavbar">
                    <ul class="  nav navbar-nav  navbar-right">
                        <li class="active">
                            <router-link @click.native="showHide()" to="/">INTRO</router-link>
                        </li>
                        <li>
                            <router-link @click.native="showHide()" to="/8go-mobifone">PROJECTS</router-link>
                        </li>
                        <li>
                            <router-link @click.native="showHide()" to="/driver">CV</router-link>
                        </li>
                        <li>
                            <router-link @click.native="showHide()" to="/booking">CONTACT</router-link>
                        </li>
                        <li>
                            <router-link @click.native="showHide()" to="/">Liên hệ</router-link>
                        </li>
                        <li>
                            <router-link @click.native="showHide()" to="/login">Đăng nhập</router-link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    </div>
</template>

<script>
    // import $ from 'jquery'
    import feather from 'feather-icons'

    export default {
        name: "Navigation",
        data() {
            return {
                isHide: 'out'
            }
        },
        mounted() {
            feather.replace()
        },
        methods: {
            showHide() {
                if (this.isHide == '') {
                    this.isHide = 'out'

                } else {
                    this.isHide = ''
                }
            }
        }
    }

</script>

<style>
    @import "../../assets/css/homepage/navigation.css";
</style>



