<template>
    <div class="home">
        <div class="container">
            <div class="row">
                <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6 ">
                    <div class="content">
                        <div class="text">
                            <h1>8Go</h1>
                            <h3>Tiết kiệm trên từng chuyến đi</h3>
                            <p>Tải ứng dụng trên Playstore hoặc Appstore</p>
                        </div>
                        <div class="action-download">
                            <a href="#">
                                <img
                                        class="img-responsive img-download" src="../../assets/images/apple-store.svg"
                                        alt="app store badge">
                            </a>
                            <a href="#">
                                <img
                                        class="img-responsive img-download"
                                        src="../../assets/images/google-play-badge.svg"
                                        alt="app store badge">
                            </a>
                        </div>
                    </div>
                </div>
                <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                    <img class="img-responsive" src="../../assets/images/booking.png" alt="">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Home",
        data() {
            return {
                srcImage: ''
            }
        },

        created() {
            this.srcImage = '../../assets/images/google-play-badge.svg'
        }
    }
</script>

<style scoped>
    @import "../../assets/css/homepage/home.css";
</style>


