import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
import Home    from '../components/homepage/Home'
// import Privacy from '../components/homepage/Privacy'
// import About from '../components/homepage/About'
// import Safety from '../components/homepage/Safety'
// import Work from '../components/homepage/Work'
// import Driver  from '../components/driver/Driver'
// import BookCar from '../components/booking/BookingMain'
// import Login   from '../components/login/Login'

const Privacy = () => import(/* webpackChunkName: "home" */ '../components/homepage/Privacy.vue')
const Safety = () => import(/* webpackChunkName: "home" */ '../components/homepage/Safety.vue')
const About = () => import(/* webpackChunkName: "about" */ '../components/homepage/About.vue')
const Work = () => import(/* webpackChunkName: "about" */ '../components/homepage/Work.vue')
const Driver = () => import(/* webpackChunkName: "about" */ '../components/driver/Driver.vue')
const BookCar = () => import(/* webpackChunkName: "about" */ '../components/booking/BookingContainer.vue')
const Login = () => import(/* webpackChunkName: "about" */ '../components/login/Login.vue')
const Mobifone = () => import(/* webpackChunkName: "about" */ '../components/homepage/Mobifone.vue')
const Why = () => import(/* webpackChunkName: "about" */ '../components/homepage/Why.vue')
const Regulation = () => import(/* webpackChunkName: "about" */ '../components/homepage/Regulation.vue')
const Regist = () => import(/* webpackChunkName: "about" */ '../components/homepage/Registration.vue')
export default new Router({

   base: process.env.BASE_URL,

    routes: [
        {
            path: '/',
            name: '8Go - Ứng dụng gọi xe sân bay VIP',
            component: Home
        },
        {
            path: '/driver',
            name: 'Trở thành lái xe',
            component: Driver
        },
        {
            path: '/booking',
            name: "Đặt xe",
            component: BookCar
        },
        {
            path: '/privacy',
            name: "Chính sách bảo mật",
            component: Privacy
        },
        {
            path: '/about',
            name: "Về chúng tôi",
            component: About
        },
        {
            path: '/login',
            name : "Đăng Nhập",
            component: Login
        },
        {
            path: '/safety',
            name : "An toàn",
            component: Safety
        },
        {
            path: '/how-it-work',
            name : "Cách thức đặt xe",
            component: Work
        },
        {
            path: '/8go-mobifone',
            name : "8GO x Mobifone",
            component: Mobifone
        },
        {
            path: '/why',
            name : "Vì sao bạn chọn 8Go",
            component: Why
        },

        {
            path: '/regulation',
            name : "Quy tắc ứng xử - quy định lái xe",
            component: Regulation
        },
        {
            path: '/regist',
            name : "Đăng kí tài xế",
            component: Regist
        }
    ],
    scrollBehavior(){
        return {x: 0, y: 0};
    },


})



